/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, { useState } from 'react';
import PropTypes from "prop-types"
import { useStaticQuery, graphql, Link } from "gatsby"

import Header from "./header"
import "./layout.css"
import style from "./common.module.scss"

const Layout = ({ children }) => {
  const [showMainMenu, setShowMainMenu] = useState(false);


  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  function onMainMenuButtonClick(){
    console.log("onMainMenuButtonClick");
    setShowMainMenu(!showMainMenu);
  }
  const onMainMenuClose = function(){
    console.log("close clicked!")
    setShowMainMenu(false)
  }

  return (
    <>
      <Header onMainMenuButtonClick={onMainMenuButtonClick} />
      <div
        style={{
          margin: `0 auto`,
          maxWidth: 1024,
          padding: `0 1.0875rem 1.45rem`,
        }}
      >
        <main>{children}</main>
        <footer>
          
        </footer>
      </div>
        <MainMenu show={showMainMenu} onMainMenuClose={onMainMenuClose} />

    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

const MainMenu = (props) => {
  
  const classNames = ( props.show ? style.mainMenuContainerShow : style.mainMenuContainer);
  return (
      <div className={classNames}>
          <button onClick={props.onMainMenuClose} style={{
            background: `none`,
            border: `none`,
            outline: `none`,
            cursor: `pointer`,
            position: `absolute`,
            right:0,
            top: 0,
            margin: `1rem`
            }}>
          <svg
            style={{
              width:`1rem`
            }}
            viewBox="0 0 40.895 40.895"
          ><path style={{
              fill:`#000`
            }}
          
           d=" M 20.445 25.395 L 33.47 38.42 L 35.95 40.9 L 40.9 35.95 L 38.42 33.47 L 25.395 20.445 L 38.42 7.42 L 40.9 4.95 L 35.95 0 L 33.47 2.47 L 20.445 15.495 L 7.42 2.47 L 4.95 0 L 0 4.95 L 2.47 7.42 L 15.495 20.445 L 2.47 33.47 L 0 35.95 L 4.95 40.9 L 7.42 38.42 L 20.445 25.395 Z "/></svg>
          </button>

          <div className={style.linksContainer}>
            <ul>
              <li>
              <Link to="/page-2/"><a onClick={props.onMainMenuClose} href="#">About</a></Link>
              </li>
              <li>
              <a href="#">Skills</a>
              </li>
              <li>
                <a href="#">Work</a>
              </li> 
              <li>
                <a href="#">Contact</a>
              </li>
            </ul>
              
              
              
              
          </div>  

      </div>
  );

}

export default Layout